import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform } from 'ionic-angular';
import { ViewChild } from '@angular/core';
import { ImageViewerController } from 'ionic-img-viewer';
/**
 * Generated class for the SemiroundPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-semiround',
  templateUrl: 'semiround.html',
})
export class SemiroundPage {

  
  
  @ViewChild('mySlider') mySlider: any;
_imageViewerCtrl: ImageViewerController;
  constructor(public navCtrl: NavController, public navParams: NavParams, imageViewerCtrl: ImageViewerController, public platform: Platform) {
  this._imageViewerCtrl = imageViewerCtrl;
  
  
  let backAction =  platform.registerBackButtonAction(() => {
        console.log("contact");
        this.navCtrl.pop();
        backAction();
      },2)
  
  
  
  }
  
   presentImage(myImage) {
    const imageViewer = this._imageViewerCtrl.create(myImage);
    imageViewer.present();
  }
  
  elenco =  [
             {
               "img": "assets/img/qaprofil/bambo/1.jpg",
               "posicao": "Semi-Round Panel",
               "nome": "QASRPR"
               
             },
             {
               "img": "assets/img/qaprofil/bambo/2.jpg",
                "posicao": "Semi-Round Panel",
               "nome": "QASRPR"
               
             },
             {
               "img": "assets/img/qaprofil/bambo/3.jpg",
              "posicao": "Semi-Round Panel",
               "nome": "QASRPR"
               
             },
             
             {
               "img": "assets/img/qaprofil/full_image/semi_round-1.jpg",
               "posicao": "Semi-Round Panel",
               "nome": "QASRPR"
             } 
             
           ]
           
           
           slideNext(){
       this.mySlider.slideNext();
     }
 
     slidePrev(){
       this.mySlider.slidePrev();
     }




















  ionViewDidLoad() {
    console.log('ionViewDidLoad SemiroundPage');
  }
  
  
    

}



